import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { Modal, Button } from 'react-bootstrap';

function GraphError({ show, onClose, errorMessage, originalError }) {
    return (
        <Modal show={show} onHide={onClose}>
            <Modal.Header closeButton>
                <Modal.Title>Error</Modal.Title>
            </Modal.Header>
            <Modal.Body>{errorMessage}
                <h3>Something is wrong. I think it is not your turn</h3>
            </Modal.Body>
            {originalError && (
                <Modal.Body>
                    Original Error: {originalError}
                </Modal.Body>
            )}
            <Modal.Footer>
                <Button variant="secondary" onClick={onClose}>
                    Close
                </Button>
            </Modal.Footer>
        </Modal>
    );
}

// Define propTypes for GraphError component
GraphError.propTypes = {
    show: PropTypes.bool.isRequired, // Assuming show is a boolean
    onClose: PropTypes.func.isRequired, // Assuming onClose is a function
    errorMessage: PropTypes.string, // Assuming errorMessage is a string
    originalError: PropTypes.string // Assuming originalError is also a string
};

// If any of the props are optional, you can remove `.isRequired` and use defaultProps to define default values

export default GraphError;
