import React, { Component } from 'react';
import PropTypes from 'prop-types'; // Import PropTypes for props validation

class ErrorBoundary extends Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false };
    }


    render() {
        if (this.state.hasError) {
            // You can render any custom fallback UI
            return <h1>Something went wrong.</h1>;
        }

        return this.props.children;
    }
}

// Define prop types for ErrorBoundary
ErrorBoundary.propTypes = {
    children: PropTypes.node.isRequired // Defines 'children' as a prop that can be anything that can be rendered: numbers, strings, elements or an array (or fragment) containing these types.
};

export default ErrorBoundary;
